import React from 'react';
import { useInView } from 'react-intersection-observer';
import { useRef, useEffect, useState } from 'react';

const CharItem = ({ item, index, onCharSelected }) => {
    // Use state to store the delay
    const [delay, setDelay] = useState(0);
    
    let imgStyle = { 'objectFit': 'cover' };
    if (item.thumbnail === 'http://i.annihil.us/u/prod/marvel/i/mg/b/40/image_not_available.jpg') {
        imgStyle = { 'objectFit': 'unset' };
    }
    
    const itemRefs = useRef([]);

    const focusOnItem = (id) => {
        itemRefs.current[id].focus();
    }
        
    const [itemRef, inViewItem] = useInView({
        triggerOnce: true,
        threshold: 0,
    });

    useEffect(() => {
        if (item) {
            // Calculate the delay based on the element's index
            let calculatedDelay = index >= 9 ? ((index - 9) % 9 + 1) * 100 : (index + 1) * 100 + 100;
            if (index === 0) {
                calculatedDelay = 100; // Set the delay to 0.1 seconds for the first element
            }
            setDelay(calculatedDelay);
        }
    }, [index]);


    return (
        <li
            className={`char__item ${inViewItem ? 'in-view' : ''}`}
            tabIndex={0}
            ref={(el) => {
                itemRefs.current[index] = el;
                itemRef(el);
            }}
            style={{ transitionDelay: `${delay}ms` }} // Apply the calculated delay
            onClick={() => {
                // Call the focusOnItem function with the index of the clicked item
                focusOnItem(index);
                onCharSelected(item.id);
            }}
            onKeyDown={(e) => {
                if (e.key === ' ' || e.key === 'Enter') {
                onCharSelected(item.id);
                focusOnItem(index);
                }
            }}
        >
        <img src={item.thumbnail} alt={item.name} style={imgStyle} />
        <div className="char__name">{item.name}</div>
        </li>
    );
};

export default CharItem;
