import React, { useState, useEffect, useRef, useMemo, useCallback } from 'react';
import { useInView } from 'react-intersection-observer';

import Spinner from '../spinner/Spinner';
import ErrorMessage from '../errorMessage/ErrorMessage';
import useMarvelService from '../../services/MarvelService';
import CharItem from '../charItem/CharItem';

import './charList.scss';

const setContent = (process, Component, newItemLoading) => {
    switch (process) {
        case 'waiting':
            return <Spinner/>;
        case 'loading':
            return newItemLoading ? <Component/> : <Spinner/>;
        case 'confirmed':
            return <Component/>;
        case 'error':
            return <ErrorMessage/>;
        default:
            throw new Error('Unexpected process state');
    }
}

const CharList = (props) => {
    
    const [charList, setCharList] = useState([]);
    const [newItemLoading, setNewItemLoading] = useState(false); // відноситься до підгрузки по кліку
    const [offset, setOffset] = useState(200);
    const [charEnded, setCharEnded] = useState(false); // властивість закінчення персонажів
    const [totalCount, setTotalCount] = useState(0);
    // State variable to track whether the "load more" button should be visible
    const [showLoadMoreButton, setShowLoadMoreButton] = useState(false);
    
    const { process, setProcess, getAllCharacters, getTotalCharactersCount } = useMarvelService();

    // Use the useInView hook to detect when the "load more" button is in view
    const [loadMoreButtonInViewRef, isLoadMoreButtonInView] = useInView({
        triggerOnce: true,
        threshold: 0,
    });

    useEffect(() => {
        setShowLoadMoreButton(isLoadMoreButtonInView);
    }, [isLoadMoreButtonInView]);


    useEffect(() => {
        onRequest(offset, true);
        setTotalCount(getTotalCharactersCount());
    }, [])
    

    // useEffect(() => {
    //     window.addEventListener('scroll', handleScroll);

    //     return () => {
    //         window.removeEventListener('scroll', handleScroll);
    //     };
    // })
  
    const handleScroll = () => {

        if (!loading && !newItemLoading && !charEnded) {
            const windowHeight = window.innerHeight;
            const documentHeight = document.documentElement.scrollHeight;
            const scrollTop = window.scrollY;

            if (windowHeight + scrollTop >= documentHeight - 100) { // You can adjust the threshold as needed
                onRequest(offset);
            }
        }
    };

    const onCharListLoaded = async (newCharList) => { // дані завантажились
        const ended = (offset + 9) === totalCount;

        setCharList([...charList, ...newCharList]);
        setNewItemLoading(false);
        setOffset(offset => offset + 9);
        setCharEnded(ended);
    };


 
    const onRequest = (offset, initial) => {
        initial ? setNewItemLoading(false) : setNewItemLoading(true);
        getAllCharacters(offset)
            .then(onCharListLoaded)
            .then(() => setProcess('confirmed'));
    }

    // Цей метод призначений для оптимізації,
    // щоб уникнути розміщення такої конструкції в методі render
    function renderItems(arr) {
        return (
        <ul className="char__grid">
            {arr.map((item, index) => (
                <CharItem
                    key={item.id}
                    item={item}
                    index={index}
                    onCharSelected={props.onCharSelected}
                />
            ))}
        </ul>
    );
    }


    const elements = useMemo(() => {
        return setContent(process, () => renderItems(charList), newItemLoading);
        // eslint-disable-next-line
    }, [process])

    return (
        <div className="char__list">
            {elements}
            <button
                ref={loadMoreButtonInViewRef}
                disabled={newItemLoading || charEnded}
                onClick={() => onRequest(offset)}
                className={`button button__main button__long ${showLoadMoreButton ? 'visible' : ''}`}>
            <div className="inner">load more</div>
            </button>
        </div>
    )
}

export default CharList;


